.result-wrapper .tile{
    background: rgba(255,255,255,0.5);
    height: 180px;
    padding: 16px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.result-wrapper .tile:hover{
    background: rgba(255,255,255,0.8);
    -webkit-transform: scale3d(1.006, 1.006, 1);
            transform: scale3d(1.006, 1.006, 1);
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.result-wrapper .tile-content > div{
    display:-ms-flexbox;
    display:flex;
}
.result-wrapper .tile-content .giver-name{
    -ms-flex-pack: start;
        justify-content: flex-start
}
.result-wrapper .tile-content .receiver-name{
    -ms-flex-pack: end;
        justify-content: flex-end
}
.result-wrapper .tile-content .icon{
    -ms-flex-pack: center;
        justify-content: center
}
.result-wrapper .tile-content > div >span:nth-child(2){
    margin-right: 8px;
    margin-left: 8px;
}

.result-wrapper .tile-content .info-footer{
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    bottom: 0;
    left: 0;
    width: 92%;
    padding: 4%;
}
.result-wrapper .tile-content .info-footer div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    font-size: 12px;
}
.result-wrapper .contact-details{
    position: absolute;
    top: -150px;
    left: 0;    
    width: 92%;
    background-color: #926f5a;;
    padding: 4% 4% 8% 4%;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    color: #eee;
}
.result-wrapper  .show-contact{
    top : 0px;
}
.result-wrapper .close-contact{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    position: absolute;
    width: 92%;
    bottom: -8px;
    -webkit-animation: bounce 1s infinite;
            animation: bounce 1s infinite;
}
.result-wrapper .contact-details .details-wrapper{
    margin-top: 4px;
    padding: 4px;
    background-color: #e8e8e8;
    border-radius: 4px;
    font-size: 12px;
    color:#000
}
.result-wrapper .contact-details .details-wrapper span{
    margin-left:4px;
}

.result-wrapper .contact-details .details-wrapper > div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

@-webkit-keyframes bounce {
	0%, 20%, 50%, 80%, 100% {
    -webkit-transform : translateY(0);
            transform : translateY(0);
  }
	40% {
    -webkit-transform : translateY(-8px);
            transform : translateY(-8px);
  }
	60% {
    -webkit-transform : translateY(-4px);
            transform : translateY(-4px);
  }
}

@keyframes bounce {
	0%, 20%, 50%, 80%, 100% {
    -webkit-transform : translateY(0);
            transform : translateY(0);
  }
	40% {
    -webkit-transform : translateY(-8px);
            transform : translateY(-8px);
  }
	60% {
    -webkit-transform : translateY(-4px);
            transform : translateY(-4px);
  }
}


